
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.home {
    height: 92.7vh;
    display: flex;
    background: royalblue;
    justify-content: space-evenly;
    align-items: center;
}

.home__main {
    width: 50%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.home__content {
    width: 90%;
    height: 45%;
    background: whitesmoke;
    border-radius: 10px;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.8);
    padding: 20px 40px;
}

.home__content h2 {
    text-align: center;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 25px;
    margin-bottom: 15px;
    color: royalblue;
}

.home__content p {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 20px;
    line-height: 25px;
    color: black;
}

.home__links {
    width: 90%;
    height: 45%;
    background: whitesmoke;
    border-radius: 10px;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.8);
    padding: 20px 30px;
}

.home__links ul {
    width: 100%;
    height: 100%;
    max-width: 550px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-self: center;
    overflow: auto;
}

.home__links ul a {
    border-right: 2px solid royalblue;
    padding: 10px;
    overflow: hidden;
}

.home__links ul a:last-child {
    border-right: 0;
}

.home__links ul a li {
    height: 100%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    transition: all ease 0.4s;
}

.home__links ul a li:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background: rgb(234, 234, 234);
}

.home__links ul a li h3 {
    text-align: center;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 25px;
    margin-bottom: 15px;
    color: royalblue;
    text-transform: uppercase;
    letter-spacing: 5px;
}

.home__links ul a li p {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 20px;
    line-height: 25px;
    color: black;
}


.home__aside {
    width: 40%;
    height: 80%;
    background: whitesmoke;
    padding: 20px 40px;
    border-radius: 10px;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.8);
}

.home__aside h3 {
    text-align: center;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 25px;
    margin-bottom: 15px;
    color: royalblue;
}

.home__aside p {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 20px;
    line-height: 25px;
    color: black;
}
.input--text {
    padding: 10px;
    border-radius: 10px;
    border: 0;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.7);
    outline: royalblue 1px solid;
}

.input--btn {
    padding: 10px 40px;
    border-radius: 10px;
    background-color: royalblue;
    border: 0;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.7);
    transition: all ease 0.3s;
    color: white;
    font-weight: 700;
    cursor: pointer;

}

.input--btn:hover {
    background-color: rgb(39, 80, 205);
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}

.input--select {
    -webkit-appearance: none;
            appearance: none;
    background-color: transparent;
    border: none;
    padding: 0 5px 0 0;
    margin: 0;
    width: 100%;
    height: 100%;
    font-family: inherit;
    font-size: inherit;
    cursor: inherit;
    line-height: inherit;
    outline: none;
    transition: all ease 0.4s;
}

.input--select::-ms-expand {
    display: none;
}

.select{
    width: 100%;
    display: flex;
    position: relative;
    border: 1px solid royalblue;
    border-radius: 10px;
    padding: 5px 15px;
    font-size: 20px;
    cursor: pointer;
    line-height: 1.1;
    background-color: #fff;
}
.select:last-child{
    position: absolute;
    right: 10px;
    color: royalblue;
}
.login{
    height:92.7vh;
    display: flex;
    background: royalblue;
    justify-content: space-evenly;
    align-items: center;
}

.login__container{
    width: 30%;
    height: 555px;
    display: flex;
    background: whitesmoke;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 40px 0;
    align-items: center;
    border-radius: 10px;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.6);
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.login__container h3{
    font-size: 35px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
    letter-spacing: 7px;
    color: royalblue;

}

.login__aside{
    width: 50%;
