.table {
    width: inherit;
    border-collapse: collapse;
}

.table thead {
    outline: 4px solid whitesmoke;
    background-color: royalblue;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    color: white;
}

.table thead th {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    font-weight: 700;
    padding: 10px 20px;
    text-align: center;
}

.table tbody td {
    padding: 5px 15px;
    text-align: center;
}

.table tbody tr {
    transition: all ease 0.2s;
    font-family: Arial, Helvetica, sans-serif;
}


.table tbody tr:nth-child(even) {
    background: rgb(208, 208, 208);
}

.table tbody tr:nth-child(odd) {
    background: rgb(241, 241, 241);
}

.table tbody tr:hover {
    background: rgb(184, 184, 184);
}
.admin {
    height: 92.7vh;
    display: flex;
    background: royalblue;
    justify-content: center;
    align-items: center;
}

.admin__content {
    width: 50%;
    height: 90%;
    display: flex;
    flex-direction: column;
    background: whitesmoke;
    border-radius: 10px;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.6);
    padding: 10px 30px;
}

.admin__content h3 {
    color: royalblue;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 25px;
    margin-bottom: 15px;
    text-transform: uppercase;
    text-align: center;
}

.container__buttons {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.admin__btn {
    position: relative;
    padding: 5px 10px;
    border: 0;
    border-radius: 5px;
    overflow: hidden;
}

.admin__btn:hover {
    cursor: pointer;
}
.admin__btn:hover::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.303);
}

.btn--warning {
    background: rgb(255, 210, 46);
    color: black;
}

.btn--danger {
    background: rgb(254, 55, 55);
    color: white;
}
header{
    display: flex;
    position: -webkit-sticky;
    position: sticky;
    background-color: whitesmoke;
    height: 7.3vh;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}
.header__logo{
    display: flex;
    width: 20%;
    padding-left: 20px;
}
.header__logo h1{
    display: flex;
    align-items: center;
    padding: 0 20px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    letter-spacing: 10px;
    font-weight: bold;
    color: royalblue;
    text-transform: uppercase;
}

#logo__img{
    height: 70px;
    width: 70px;
    color: royalblue;
}

.header__separator{
    width: 40%;
}

ul,li,a{
    list-style: none;
    text-decoration: none;
    color: white;
}

nav{
    width: 60%;
    display: flex;
    justify-content: center;
}
nav ul{
    display: flex;
    flex-direction: row;
    width: 80%;
    justify-content: space-around;
    align-items: center
}
nav ul li{
    background: rgb(33, 81, 226);
    padding: 10px 40px;
    border-radius: 60% 40% 62% 38% / 0% 100% 0% 100%;
    border-bottom: 5px solid rgba(0, 0, 0, 0.299);
    font-size: larger;
    transition: all ease 0.4s;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
nav ul li:hover{
    border-bottom: 7px solid rgba(0, 0, 0, 0.299);
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
}
nav ul li:active{
    border-bottom: 0;
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
}

